<template>
  <div>
  <Card class="citask-card" v-if="item.Display" :bordered="false">
    <div class="citask-card-title">
      <router-link style="color: inherit;text-decoration:underline;" :to="'/ci/task/' + item.id + '/history'">
          {{ item.TaskName }}
      </router-link>
      <span class="pull-right" style="padding-right: 10px">
          <!--<a :href="item.TaskConfigURI"><Icon type="ios-settings" :size="18" color="#555" style="margin-left: 10px;"></Icon></a>-->
      </span>
    </div>
    <div class="citask-card-body">
      <span style="width: 220px;display: inline-block;text-align: left">
          <span style="margin-top: 25px;margin-left: 0px;">
            <Icon v-if="item.LastRunStatus ===0" type="ios-time-outline" style="color:#337ab7;"></Icon>
            <Icon v-if="item.LastRunStatus ===1" type="ios-bicycle" style="color:#388e83"></Icon>
            <Icon v-if="item.LastRunStatus ===2" type="ios-checkmark-circle" style="color:#388e83"></Icon>
            <Icon v-if="item.LastRunStatus ===4" type="ios-close-circle" style="color:darkred"></Icon>
            <Icon v-if="item.LastRunStatus ===3" type="ios-remove-circle" style="color:olive"></Icon>
            {{ item.LastRunTime }} </span>
        </span>
      <span style="width:80px;margin-top:25px; display: inline-block">
        <Dropdown v-if="item.ParameterGroups.length > 0" trigger="click" @on-click="onDPItemClick" >
          <Button type="default"  shape="circle" style="padding: 3px 15px;">
            构建
            <Icon type="md-arrow-dropdown" />
          </Button>
         <DropdownMenu slot="list" style="max-height: 500px;overflow-y: scroll;top:0px;">
            <DropdownItem v-for="dpitem in item.ParameterGroups" :key="dpitem.id" :name="dpitem.id+','+item.id" ><Icon v-if="dpitem.default" type="checkmark-round"></Icon> {{ dpitem.title }}</DropdownItem>
         </DropdownMenu>
         </Dropdown>
          <Button v-if="item.ParameterGroups.length ===0" type="default"  shape="circle" @click="onBuildClick(item.id)" style="padding: 3px 15px;">
            构建
        </Button>
        </span>
    </div>

  </Card>
    <Modal v-model="showParameterDialog" title="构建参数" :mask-closable="false" @on-ok="startBuildTask(selectedParameterGroup.id)" @on-cancel="closeParameterDialog">
      <Form ref="resloveIssue" :model="formItem" :label-width="80">
        <FormItem v-for="parameter in selectedParameterGroup.parameters" :key="parameter.key"  :label="parameter.key" prop="ResloveResult">
          <Input :name="parameter.key" :value="parameter.value" :desc="parameter.description" placeholder="任务流名称50个字符以内！" />
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
export default {
  name: 'ciTaskItem',
  props: ['item'],
  data () {
    return {
      showParameterDialog: false,
      formItem: {},
      selectedParameterGroup: {}
    }
  },
  methods: {

    onDPItemClick: function (itemID) {
      this.showParameterDialog = true
      let taskStartParameters = itemID.split(',')
      this.selectedParameterGroup = this.getParameterGroup(taskStartParameters[0])
    },

    getParameterGroup: function (parameterID) {
      let result = {}
      for (let i = 0; i< this.item.ParameterGroups.length; i++) {
        if (this.item.ParameterGroups[i].id  === parameterID) {
          result = this.item.ParameterGroups[i]
          break
        }
      }
      return result
    },

    closeParameterDialog: function () {
      this.showParameterDialog = false
    },

    startBuildTask (parameterID) {
      let parameters = {parameters: []}
      for (let i = 0; i < this.$refs['resloveIssue'].$el.length; i++) {
        let temp = {}
        temp['key'] = this.$refs['resloveIssue'].$el[i].name
        temp['value'] = this.$refs['resloveIssue'].$el[i].value
        temp['description'] = this.$refs['resloveIssue'].$el[i].desc
        parameters.parameters.push(temp)
      }


      this.$axios.patch('/api/ci/task/parameter_group/' + parameterID + '/',parameters).then(response => {
        this.$axios.get('/api/ci/task/' + this.item.id + '/start/?BuildParameter=' + parameterID).then(response => {
          this.$Message.success({
            content: response.data.result.message,
            duration: 10
          })
        }, response => {
          this.$Message.error({
            content: response.data.result.message,
            duration: 10
          })
        })
      }, response => {
        this.$Message.error({
          content: response.data.result.message,
          duration: 10
        })
      })

      this.showParameterDialog = false
    },


    onBuildClick (taskID) {
      this.$axios.get('/api/ci/task/' + taskID + '/start/').then(response => {
        this.$Message.success({
          content: response.data.result.message,
          duration: 10
        })
      }, response => {
        this.$Message.error({
          content: response.data.result.message,
          duration: 10
        })
      })
    },
  },

  created: function () {
  },
  watch: {
    item: function (value) {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .citask-card{
    width: 350px;
    min-height: 100px;
    float: left;
    margin-left: 10px;
    margin-top: 10px;
  }
  .citask-card-title
  {
    margin-left: -16px;
    margin-right: -16px;
    margin-top: -10px;
    padding: 0px;
    padding-left: 15px;
    height: 30px;
    text-align: left;
    border-bottom: 1px solid #f5f7f9
  }
  .citask-card-body{

  }
</style>



